"use client";

import React, { useRef } from "react";
import Sidebar from "@/components/sidebar";
import VaccinePage from "./page1";
import Footer from "@/components/footer";

// Import PDF libraries
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

const MainVaccinePage = () => {
  const pdfRef = useRef(null);

  const handleSavePDF = async () => {
    if (pdfRef.current) {
      // Capture the VaccinePage element as a canvas
      const canvas = await html2canvas(pdfRef.current, { scale: 2 });
      const imgData = canvas.toDataURL("image/png");

      // Create a new jsPDF instance (portrait, mm units, A4 paper)
      const pdf = new jsPDF("p", "mm", "a4");

      // Calculate dimensions to fit the image in the PDF
      const pdfWidth = pdf.internal.pageSize.getWidth();
      const imgProps = pdf.getImageProperties(imgData);
      const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;

      // Add the captured image to the PDF and save it
      pdf.addImage(imgData, "PNG", 0, 0, pdfWidth, pdfHeight);
      pdf.save("vaccine-page.pdf");
    }
  };

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      {/* Sidebar */}
      <Sidebar />
      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        {/* Save to PDF Button */}
        <button
          onClick={handleSavePDF}
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
        >
          Save to PDF
        </button>

        {/* Wrap VaccinePage in a container with a ref to capture it */}
        <div ref={pdfRef}>
          <VaccinePage />
          <VaccinePage />
        </div>
      </main>

      <Footer />
    </div>
  );
};

export default MainVaccinePage;
